import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import IconButton from '@material-ui/core/IconButton';
import RecivedIcon from '@material-ui/icons/Done';
import Typography from '@material-ui/core/Typography';
import firebase from 'firebase'


const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function AlertDialogSlide(props) {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleYesClciked=()=>{
    const {userId,productId} = props
    let data
    console.log(userId,productId)

    let docRef = firebase.firestore().collection("users").doc(userId);

        docRef.get().then(function(doc) {
          if (doc.exists) {
           
              data = doc.data().currentlyLending
              console.log(data)
              const index = data.findIndex(x => x.id ===productId)

              const temp = [...data]
              temp.splice(index,1)
              console.log(temp)
              let newProd

              docRef = firebase.firestore().collection("products").doc(productId);
              docRef.get().then(function(doc) {
                newProd = [...doc.data().serials, ...data[0].serial]
                console.log(newProd)

              }).then(function(){
                firebase.firestore().collection("products").doc(data[index].id).set({
                  serials:newProd
                },{merge:true})
                .then(function() {
                    console.log("Document successfully written!");
                    firebase.firestore().collection("users").doc(userId).update({
                      currentlyLending:temp
                    })
                    .then(function() {
                        console.log("Document successfully written!");
  
                        refreshPage()
                        
                                     
                    })
                    .catch(function(error) {
                        console.error("Error writing document: ", error);
                    });
                })
                .catch(function(error) {
                    console.error("Error writing document: ", error);
                });

              }).catch(function(error) {
                console.log("Error getting document:", error);
              })

              

 

          } else {
              console.log("No such document!");
          }
        }).catch(function(error) {
          console.log("Error getting document:", error);
        });
    
    handleClose()
  
  }

  function refreshPage() {
		window.location.reload(false);
	}

  return (
    <div>
       <IconButton color="primary" component="span" onClick={handleClickOpen}><RecivedIcon></RecivedIcon></IconButton>
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle id="alert-dialog-slide-title">{"Have you Received:"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-slide-description">
  <Typography component="h5" variant="h5" color="primary" gutterBottom>{props.quantity} {props.productName} from {props.userName}</Typography>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            NO
          </Button>
          <Button onClick={handleYesClciked} color="primary">
            YES
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}