import React, { useState, useEffect } from 'react'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogTitle from '@material-ui/core/DialogTitle'
import Slide from '@material-ui/core/Slide'
import Typography from '@material-ui/core/Typography'
import firebase from 'firebase'
import emailjs from 'emailjs-com'

import InfiniteCalendar from 'react-infinite-calendar'
import 'react-infinite-calendar/styles.css' // Make sure to import the default stylesheet

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />
})
const userId = 'user_f9Weuah3b6knK7v7GIzLf'
export default function AlertDialogSlide(props) {
  const [open, setOpen] = useState(false)
  const [reserveDate, setReserveDate] = useState()
  const [showInvalid, setShowInvalid] = useState(false)

  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  useEffect(() => {
    setShowInvalid(false)
  }, [])

  const handleReserve = async () => {
    if (reserveDate === undefined) {
      setShowInvalid(true)
      return
    }

    const ref = firebase
      .firestore()
      .collection('reserves')
      .doc(firebase.auth().currentUser.uid)

    let oldData = []
    const dataToSend = {
      productName: props.product.name,
      productId: props.product.id,
      model: props.product.model,
      reserveDate: reserveDate,
    }

    ref.get().then((res) => {
      if (res.data() !== undefined) {
        const data = res.data().reserves
        data.map((e) => oldData.push(e))
        oldData.push(dataToSend)
      }
      ref
        .set({
          reserves: oldData.length === 0 ? [dataToSend] : oldData,
        })
        .then(() => {
          sendReserveMail(dataToSend)
          handleClose()
        })
    })
  }

  const sendReserveMail = (dataToSend) => {
    var service_id = 'default_service'
    var template_id = 'template_oEP0naaV_clone'
    emailjs.init(userId)
    var template_params = {
      person: undefined,
      school: undefined,
      item: dataToSend.productName,
      time: dataToSend.reserveDate,
      personEmail: undefined,
    }

    firebase
      .firestore()
      .collection('users')
      .doc(firebase.auth().currentUser.uid)
      .get()
      .then((res) => {
        template_params.person =
          res.data().firstName + ' ' + res.data().lastName
        template_params.school = res.data().school
        template_params.personEmail = res.data().email
        console.log(template_params)
        emailjs.send(service_id, template_id, template_params)
      })
  }

  const handleDateChange = (d) => {
    setReserveDate(new Date(d).toDateString())
    setShowInvalid(false)
  }

  return (
    <div>
      <Button
        variant="outlined"
        style={{ marginTop: 20 }}
        color="primary"
        onClick={handleClickOpen}
      >
        RESERVE IT
      </Button>
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle id="alert-dialog-slide-title">
          {props.productName}
        </DialogTitle>
        <DialogContent style={{ marginLeft: 45 }}>
          <InfiniteCalendar
            minDate={new Date()}
            max={
              new Date(new Date().getFullYear(), new Date().getMonth() + 2, 31)
            }
            width={380}
            height={320}
            disabledDays={[0, 2, 3, 4, 5, 6]}
            onSelect={handleDateChange}
          />
        </DialogContent>
        <Typography style={{ textAlign: 'center', padding: 10 }} variant="h6">
          Pick a date and BotsIQ will send you a email with more details soon.
        </Typography>
        {showInvalid ? (
          <Typography
            color="textSecondary"
            style={{ textAlign: 'center', padding: 10 }}
            variant="h6"
          >
            Pick a valid date!
          </Typography>
        ) : null}

        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleReserve} color="primary">
            Send Request to RESERVE
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}
