import React,{useState,useEffect} from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import ProductsTable from './AdminTable';
import firebase from 'firebase'

const AdminUserManager = props => {

  const [userData,setUserData] = useState([])
  const [alreadyGotData,setAlreadyGotData] = useState(false)
  let tempData = []


  const getData = async()=>{

    let tempUserData = [];
    
    let usersRef = firebase.firestore().collection('users');
    let allUsers = usersRef
  .get()
  .then(snapshot => {
    snapshot.forEach(doc => {
          if(doc.data().currentlyLending !== undefined && doc.data().currentlyLending.length > 0){
            const id = doc.id
            let tempUserData = doc.data()
            tempUserData.id = id
            tempData.push(tempUserData)
            setUserData(tempData)
      }

    });
  })
  .catch(err => {
    console.log('Error getting documents', err);
  });


  }
  useEffect(() => {
    if(alreadyGotData!=true){
      getData()
      setAlreadyGotData(true)
      console.log('HERE')
    }
    setAlreadyGotData(true)
    setAlreadyGotData(false)
    setAlreadyGotData(true)

  },[]);
  
  if(alreadyGotData){
    return(
      <Grid item xs={12}>
        <Typography component="h3" variant="h5" color="primary" gutterBottom>Order Details</Typography>
        <ProductsTable data={userData} />
      </Grid>
    )
  }else{
    return null
  }



  
};

AdminUserManager.propTypes = {
  products: PropTypes.array,
};

const mapStateToProps = state => {
  const {
    firestore: {
      ordered: { products },
    },
  } = state;
  return {
    products,
  };
};

export default connect(mapStateToProps)(AdminUserManager);

