import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';


const useStyles = makeStyles(theme => ({
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
  button: {
    margin: theme.spacing(1),
  },
  link: {
    textDecoration: 'none',
    color: 'dodgerblue',
    display: 'flex',
    alignItems: 'center',
  },
}));

const OrderTable = props => {
  const classes = useStyles();
  const { products } = props;

  return (
    <Grid item xs={12}>
      <Paper className={classes.paper}>
        <Table size="medium">
          <TableHead>
            <TableRow>
              <TableCell>Expected to Receive</TableCell>
              <TableCell>Product Name</TableCell>
              <TableCell>Product Model</TableCell>
              <TableCell>Requested Quantity</TableCell>
              <TableCell>NOTE</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {products && products.length > 0 ? (
              products.map(product => (
                <TableRow key={product.id}>
                  <TableCell>
                      {new Date(product.lendTime).toDateString()}
                    {/* {Moment(product.lendTime.format('DD MMMM YY'))} */}
                  </TableCell>
                  <TableCell>{product.productName}</TableCell>
                  <TableCell>{product.model}</TableCell>
                  <TableCell>{product.quality}</TableCell>
                  <TableCell>{product.note}</TableCell>
                </TableRow>
              ))
            ) : (
              <TableRow>
                <TableCell>There are no Order Items</TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </Paper>
    </Grid>
  );
};

export default OrderTable;
